package eu.karelhovorka.textgame.core.validator

import eu.karelhovorka.textgame.core.bo.GameConfig
import eu.karelhovorka.textgame.core.bo.Move
import eu.karelhovorka.textgame.core.bo.State
import eu.karelhovorka.textgame.core.exception.MoveException
import eu.karelhovorka.textgame.core.exception.StateException
import eu.karelhovorka.textgame.core.util.GameConfigHelper


val STATE_ID_REGEX = "u[0-9A-Za-z_]*".toRegex()
val MOVE_ID_REGEX = "h[0-9A-Za-z_]*".toRegex()

fun getFlags(gameConfig: GameConfig): Array<String> {
    val flags = ArrayList<String>()
    for (move in gameConfig.moves) {
        flags.add(move.id)
        flags.addAll(move.flags)
    }
    for (state in gameConfig.states) {
        flags.add(state.id)
        flags.addAll(state.flags)
    }
    gameConfig.chapters.forEach { it.flagGroups.forEach { flags.addAll(it.flags) } }
    return flags.toTypedArray()
}


data class ValidatorWarning(val message: String, val state: State? = null, val move: Move? = null)

class GameValidator(val gameConfig: GameConfig) {
    val helper = GameConfigHelper(gameConfig)
    val warningList = mutableListOf<ValidatorWarning>()

    var flags = getFlags(gameConfig)

    fun warn(message: String, state: State? = null, move: Move? = null) {
        println("$message - $state - $move")
        warningList.add(ValidatorWarning(message = message, state = state, move = move))
    }

    fun validateStates() {
        validateNotDuplicitStateIds()
        for (state in gameConfig.states) {
            validateState(state)
        }
    }

    fun validateNotDuplicitStateIds() {
        val duplicitIds = mutableListOf<String>()
        for (state in gameConfig.states) {
            if (duplicitIds.contains(state.id)) {
                warn("Duplicitní stav!\nKIKI to znamená, že je několikrát v textu tohle id, nejspíš copy-paste překlep.", state)
            }
            duplicitIds.add(state.id)
        }
    }

    private fun validateState(state: State) {
        if (!STATE_ID_REGEX.matches(state.id)) {
            warn(
                    message = "Invalid state id: ${state.id} \nKIKI, má přece začínat na 'u'!",
                    state = state
            )
        }
        validateStateMovesExist(state)
        validateMovesNotAmbiguous(state)
        validateMovesAllHaveImageOrNot(state)
    }

    private fun validateMovesNotAmbiguous(state: State) {
        val moves = helper.movesOf(state)
        val autoMoveNoRestrictionMoves = moves.filter { m -> m.isAutoMove() && m.flagsDisallowing.isEmpty() && m.flagsRequired.isEmpty() }
        if (autoMoveNoRestrictionMoves.size > 1) {
            val moveIds = autoMoveNoRestrictionMoves.map { it.id }.toList()
            warn(
                    message = "Multiple automoves $moveIds with no flag restrictions for state \nTo znamená, že je tam víc tahů, který nemaj popisek. Buď musí mít všechny krom 1 popisek, nebo musí bejt pošéfovaný pomocí podminena/vyloucena, aby vždycky takovej byl maximálně jeden, ten 'automatickej'",
                    state = state
            )
        }
    }

    private fun validateMovesAllHaveImageOrNot(state: State) {
        val moves = helper.movesOf(state)
        val movesWithoutImages = moves.filter { it.image == null }.map { it.id }.toList()
        val movesWithImages = moves.filter { it.image != null }.map { it.id }.toList()
        if (movesWithImages.isNotEmpty() && movesWithoutImages.isNotEmpty()) {
            warn(
                    message = "Some moves have images $movesWithImages and some don't $movesWithoutImages. \nKIKI to znamená, že některý tahy tohodle stavu maj nastavenej obrázek a některý nemaj!",
                    state = state
            )
        }
    }

    private fun validateStateMovesExist(state: State) {
        for (moveId in state.moveIds) {
            try {
                helper.moveById(moveId)
            } catch (e: Exception) {
                warn(
                        message = "Cannot find move $moveId from state ${state.id}",
                        state = state
                )
            }
        }
    }


    fun validateMoves(ignoreAcrossChapters: Boolean = true) {
        validateNotDuplicitMoveIds()
        for (move in gameConfig.moves) {
            validateMove(move, ignoreAcrossChapters = ignoreAcrossChapters)
        }
    }

    fun validateNotDuplicitMoveIds() {
        val duplicitIds = mutableListOf<String>()
        for (move in gameConfig.moves) {
            if (duplicitIds.contains(move.id)) {
                warn(
                        message = "Move ${move.id} already in text!\n KIKI to znamená, že je několikrát v textu tohle id hrany, nejspíš copy-paste překlep.",
                        move = move
                )
            }
            duplicitIds.add(move.id)
        }
    }

    private fun validateMove(move: Move, ignoreAcrossChapters: Boolean = true) {
        if (!MOVE_ID_REGEX.matches(move.id)) {
            warn(
                    message = "Invalid move id: ${move.id} \nKIKI to znamená, že tah musí začínat na 'h'",
                    move = move
            )
        }
        try {
            helper.stateById(move.targetId)
        } catch (e: Exception) {
            if (move.acrossChapters() && ignoreAcrossChapters) {
                //it's move across chapters, so we hope it is just move to next chapter
            } else {
                warn(
                        message = "Cannot find target ${move.targetId} from move ${move.id} \nKIKI to znamená, že hrana '${move.id}' má špatně nastavenej 'cil'",
                        move = move
                )
            }
        }
        for (flag in move.flagsDisallowing) {
            if (flag !in flags) {
                warn(
                        message = "Non-existent flagsDisallowing $flag for move ${move.id} \nKIKI to znamená, že ta vlajka neexistuje - neni nikde nastavená pomocí příznaku, ani to neni existující tah/uzel. Jo a oddělujou se čárkou!",
                        move = move
                )
            }
        }
        for (flag in move.flagsRequired) {
            if (flag !in flags) {
                warn(
                        message = "Non-existent flagsRequired $flag for move ${move.id} \n" +
                                "KIKI to znamená, že ta vlajka neexistuje - neni nikde nastavená pomocí příznaku, ani to neni existující tah/uzel. Jo a oddělujou se čárkou!",
                        move = move
                )
            }
        }
    }

    fun validate() {
        warningList.clear()
        validateMoves()
        validateStates()
        if (warningList.isNotEmpty()) {
            val firstWarning = warningList.first()
            if (firstWarning.state != null) {
                throw StateException(firstWarning.state, firstWarning.message)
            } else if (firstWarning.move != null) {
                throw MoveException(firstWarning.move, firstWarning.message)
            }
            throw IllegalArgumentException(warningList.first().toString())
        }
    }

    fun warnings(): Array<ValidatorWarning> {
        warningList.clear()
        validateMoves()
        validateStates()
        return warningList.toTypedArray()
    }

}