package eu.karelhovorka.textgame.core.util

fun <T> Array<T>.itemsEqual(other: Array<T>?): Boolean {
    if (this == other)
        return true
    if (other == null)
        return false

    val length = this.size
    if (other.size != length)
        return false

    for (i in 0 until length) {
        val o1 = this[i]
        val o2 = other[i]
        if (!(if (o1 == null) o2 == null else o1 == o2))
            return false
    }

    return true
}

fun <T> Array<T>.itemsHashCode(): Int {
    if (this == null)
        return 0

    var result = 1

    for (element in this)
        result = 31 * result + (element?.hashCode() ?: 0)

    return result
}