package eu.karelhovorka.textgame.core.util

import eu.karelhovorka.textgame.core.common.RandomUtil

fun <E> List<E>.random() = this[RandomUtil.nextInt(this.size)]

fun <E> MutableList<E>.addUnique(item: E) {
    if (this.contains(item)) {
        return
    }
    this.add(item)
}

fun <E> Array<E>.random() = this[RandomUtil.nextInt(this.size)]

fun <E> Set<E>.random(): E {
    val rnd = RandomUtil.nextInt(this.size)
    var index = 0
    for (item in this) {
        if (rnd == index) {
            return item
        }
        index += 1
    }
    throw IllegalStateException("should not reach")

}

fun <T> MutableList<T>.removeLast() = this.removeAt(this.size -1)
fun <T> MutableList<T>.push(item: T) = this.add(item)
fun <T> MutableList<T>.push(items: Collection<T>) = items.forEach { this.push(it) }
fun <T> List<T>.peek() : T? = this.lastOrNull()
    fun <T> MutableList<T>.pop() : T? {
    val item = this.lastOrNull()
    if (!isEmpty()){
        this.removeLast()
    }
    return item
}