package eu.karelhovorka.textgame.core.util


fun <K,V> Map<K,V>.getOrThrow(key: K): V {
    val result = this.get(key)
    if (result == null) {
        throw IllegalStateException("Key not found in map $key")
    }
    return result
}

fun <K,V> Map<K,V>.throwIfExists(key: K) {
    if (this.containsKey(key)) {
        throw IllegalStateException("Key found in map $key")
    }
}

fun <K,V> Map<K,V>.toPairs(): Array<Pair<K, V>> {
    return this.map { it.key to it.value }.toTypedArray()
}