package eu.karelhovorka.textgame.core.bo

import eu.karelhovorka.textgame.core.build.GameConverter

data class GameConfig(val startId: String, val states: Array<State>, val moves: Array<Move>, val chapters: Array<Chapter>) {

    companion object {
        fun fromText(text: String): GameConfig {
            return GameConverter().convert(text)
        }
    }

}
